import React from 'react';
import styled from 'styled-components';
import { format } from 'date-fns';

import { Container } from 'components/layout/Container';
import { colors, breakpoints, space } from 'utils/variables';
import { SocialMedia } from 'interfaces/gatsby';
import { Text, Paragraph, Heading } from 'components/foundations';
import { Link } from 'gatsby';
import logo from 'assets/images/logo-white.svg';
import SocialIcons from './SocialIcons';
// https://webdesign.tutsplus.com/tutorials/how-to-build-a-responsive-multi-level-sticky-footer-with-flexbox--cms-33341
// https://webdesign.tutsplus.com/tutorials/how-to-build-a-full-screen-responsive-page-with-flexbox--cms-32086
interface FooterProps {
  version: string;
  siteLastUpdated: string;
  socials?: SocialMedia[];
}

const Footer = styled('footer')``;

const Section = styled('section')`
  padding: 1.25rem 1.875rem;
  display: flex;
  flex-wrap: wrap;
  background-color: ${colors.grey07};
`;

const LegalSection = styled('section')`
  display: flex;
  flex-wrap: wrap;
  background-color: ${colors.grey10};
  padding: 0.9375rem 1.875rem;
`;

const Item = styled('div')`
  padding: 1.25rem;
  min-width: 12.5rem;
`;

const Title = styled('h2')`
  color: ${colors.white};
  font-size: 1.375rem;
  padding-bottom: 0.625rem;
  padding-left: 0.625rem;
`;

const ItemList = styled('ul')`
  list-style: none;
  padding-left: 0.625rem;
`;

const ItemListItem = styled('li')`
  list-style: none;
  padding-top: 5;
  font-size: 1.3rem;
  color: ${colors.white};
`;

const SocialSection = styled('section')`
  padding: 0 1.875rem 1.25rem;
  background-color: ${colors.grey07};
  @media (min-width: ${breakpoints.xs}px) {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 30px;
  }
`;

const LegalList = styled('ul')`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
`;

const LegalListItem = styled('li')`
  margin: 0.125rem 0.625rem;
  white-space: nowrap;
  &:last-child(2) {
    text-decoration: none;
  }
`;

const PALFooter: React.FC<FooterProps> = ({ version, siteLastUpdated, socials }) => {
  const date = new Date(siteLastUpdated);
  return (
    <Footer>
      <Section>
        <Item>
          <Link to="/">
            <img src={logo} alt="PALPortals" style={{"padding" : "12px 0px"}}/>
          </Link>
          <Paragraph color={colors.white} margin="15">Copyright &copy; {format(date, 'yyyy')} PALPortals.com</Paragraph>
          <Paragraph color={colors.white}>All rights reserved</Paragraph>
        </Item>
        <Item>
          <Title color="white">About</Title>
          <ItemList>
            <ItemListItem><Link to="/Legal">Legal</Link></ItemListItem>
            <ItemListItem><Link to="/Terms-and-Conditions">Terms and Conditions</Link></ItemListItem>
            <ItemListItem><Link to="/Privacy-and-Policy">Privacy and Policy</Link></ItemListItem>
            <ItemListItem><Link to="#">Customers</Link></ItemListItem>
            <ItemListItem><Link to="#">Careers</Link></ItemListItem>
          </ItemList>
        </Item>
      </Section>
      <SocialIcons socials={socials} />
      <LegalSection>
        <LegalList>
          <LegalListItem>
            <Link to="#">Terms &amp; Conditions</Link>
          </LegalListItem>
          <LegalListItem>
            <Link to="#">Privacy Policy</Link>
          </LegalListItem>
          <LegalListItem>
            <Text as="p" size={200} color="red07">
              Version {version}. Last updated {format(date, 'd mmmm yyyy')}.
            </Text>
          </LegalListItem>
        </LegalList>
      </LegalSection>
    </Footer>
  );
};

export default PALFooter;
