import React from 'react';
import { OutboundLink } from 'gatsby-plugin-google-analytics';
import { colors, breakpoints, space } from 'utils/variables';
import styled from 'styled-components';
import Github from '../../foundations/icons/Github';
import Facebook from '../../foundations/icons/Facebook';
import LinkedIn from '../../foundations/icons/LinkedIn';
import Icon from '../../foundations/icons/Icon';
import Twitter from '../../foundations/icons/Twitter';

const icons = {
  Twitter: <Twitter />,
  Github: <Github />,
  Facebook: <Facebook />,
  LinkedIn: <LinkedIn />
};

const SocialIcons = ({ socials }) => {
  return (
    <SocialSection>
      <SocialList>
        {socials.map((social, i) => (
          <SocialListItem key={i}>
            <SocialMediaLink href={social.url} target="_blank" rel="noopener noreferrer">
              <Icon icon={icons[social.name]} size={32} fillColor={colors.white} focusColor={colors.red01}/>
            </SocialMediaLink>
          </SocialListItem>
        ))}
      </SocialList>
    </SocialSection>
  );
};

const SocialSection = styled('section')`
  padding: 0 1.875rem 1.25rem;
  background-color: ${colors.grey07};
  align-items: center;
  @media (min-width: ${breakpoints.xs}px) {
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 30px;
  }
`;

const SocialList = styled('ul')`
  display: flex;
  justify-content: center;
  border-top: 1px #777 solid;
  padding-top: 1.25rem;
  align-items: center;
  list-style-type: none;
`;

const SocialListItem = styled('li')`
  margin: 0.5rem;
  font-size: 1.25rem;
`;

const SocialMediaLink = styled(OutboundLink)`
`;

export default SocialIcons;
