import React from 'react';
import styled from 'styled-components';
import { format } from 'date-fns';
import { OutboundLink } from 'gatsby-plugin-google-analytics';
import { Container } from 'components/layout/Container';
import { colors, breakpoints, space } from 'utils/variables';
import { SocialMedia } from 'interfaces/gatsby';
import { Text, Paragraph, Heading } from 'components/foundations';
import { Link } from 'gatsby';
import logo from 'assets/images/logo-white.svg';
interface FooterProps {
  version: string;
  siteLastUpdated: string;
  socials?: SocialMedia[];
}

const Wrapper = styled('footer')`
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  position: relative;
  padding: 2px;
  padding-top: 30px;
  // border-top: 20px solid ${colors.grey02};
  background-color:${colors.indigo09};
  height: 350px;
  @media (min-width: ${breakpoints.md}px) {
    // flex-direction: row;
    // align-items: center;
    // justify-content: space-between;
    padding-bottom: 30px;
  }
`;

const Inner = styled('div')`
  display: flex;
  flex-direction: column;
  padding: 24px;
  @media (min-width: ${breakpoints.md}px) {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
`;

const section = styled('div')`
  color: ${colors.white};
`;

const SocialMediaList = styled('ul')`
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
`;

const SocialMediaListItem = styled('li')`
  margin: 0;
  margin-left: 8px;
  margin-right: 8px;

  &:first-child {
    margin-left: 0;
  }

  &:last-child {
    margin-right: 0;
  }
`;

const SocialMediaLink = styled(OutboundLink)`
  display: inline-block;
  color: inherit;
  vertical-align: middle;
  width: 16px;
  height: 16px;

  &:hover,
  &:focus {
    text-decoration: none;
  }

  img {
    width: 16px;
    height: 16px;
  }
`;

const FooterSection = styled('section')`
  @media (max-width: ${breakpoints.md - 1}px) {
    &:not(:first-child) {
      margin-top: ${space.sm}px;
    }
  }
`;

const Footer: React.FC<FooterProps> = ({ version, siteLastUpdated, socials }) => {
  const date = new Date(siteLastUpdated);

  return (
    <Wrapper>
      <Container>
        <Inner>
          <FooterSection>
            <section>
              <Link to="/">
                <img src={logo} alt="PALPortals" />
              </Link>
              <Paragraph color="white">Copyright &copy; {format(date, 'yyyy')} PALPortals.com.</Paragraph>
              <Paragraph color="white">All rights reserved</Paragraph>
            </section>
          </FooterSection>
          <FooterSection>
            <section>
              <Heading fontSize="500" color="white" >About</Heading>
              <Paragraph><Link to="/" color="red06" >Legal</Link></Paragraph>
              <Paragraph><Link to="/" color="red06" >Terms and Conditions</Link></Paragraph>
              <Paragraph><Link to="/" color="red06" >Privacy</Link></Paragraph>
            </section>
          </FooterSection>
          <FooterSection>
            <section>
              <Heading fontSize="500" color="white" >About</Heading>
              <Paragraph><Link to="/" color="red06" >Legal</Link></Paragraph>
              <Paragraph><Link to="/" color="red06" >Terms and Conditions</Link></Paragraph>
              <Paragraph><Link to="/" color="red06" >Privacy</Link></Paragraph>
            </section>
          </FooterSection>
          <FooterSection>
            <section>
              <Heading fontSize="500" color="white" >About</Heading>
              <Paragraph><Link to="/" color="red06" >Legal</Link></Paragraph>
              <Paragraph><Link to="/" color="red06" >Terms and Conditions</Link></Paragraph>
              <Paragraph><Link to="/" color="red06" >Privacy</Link></Paragraph>
            </section>
          </FooterSection>
          {socials && (
            <FooterSection>
              <SocialMediaList>
                {socials.map((social, i) => (
                  <SocialMediaListItem key={i}>
                    <SocialMediaLink href={social.url} target="_blank" rel="noopener noreferrer">
                      <img src={`/images/${social.imgpath}`} alt={social.name} />
                    </SocialMediaLink>
                  </SocialMediaListItem>
                ))}
              </SocialMediaList>
            </FooterSection>
          )}
        </Inner>
        {/* <Text as="p" size={200} color="red07">
              Version {version}. Last updated {format(date, 'D MMMM yyyy')}.
            </Text> */}
      </Container>
    </Wrapper>
  );
};

export default Footer;
