import React from 'react';
import styled from 'styled-components';

const Icon = ({ icon, size }) => <StyledIcon size={size}>{icon}</StyledIcon>;

const StyledIcon = styled('span')`
  display: inline-block;
  width: ${p => `${p.size}px`};
  height:${p => `${p.size}px`};
`;

export default Icon;
